$spacer: 16px;
$spacers: ();

// stylelint-disable-next-line scss/dollar-variable-default
$spacers: map-merge((
  0: 0,
  1: ($spacer * 0.125), // 2px
  2: ($spacer * 0.25), // 4px
  3: ($spacer * 0.375), // 6px
  4: ($spacer * 0.5), // 8px
  5: ($spacer * 0.75), // 12px
  6: $spacer, // 16px
  7: ($spacer * 1.25), // 20px
  8: ($spacer * 1.5), // 24px
  9: ($spacer * 2), // 32px
  10: ($spacer * 4), // 64px
  11: ($spacer * 6) // 96px
), $spacers);
