@import 'bootstrap/scss/tables';

.table {
  border-radius: $border-radius;
  background-color: var(--bs-table-bg);

  th,
  td {
    vertical-align: middle;

    &:first-child {
      padding-inline-start: map-get($spacers, 6);
    }

    &:last-child {
      padding-inline-end: map-get($spacers, 6);
    }
  }

  tbody tr {
    &:last-child {
      border-end-start-radius: $border-radius;
      border-end-end-radius: $border-radius;

      > * {
        border-block-end: 0;
      }

      > :first-child {
        border-end-start-radius: inherit;
      }

      > :last-child {
        border-end-end-radius: inherit;
      }
    }
  }

  thead {
    border-width: 0;
    border-block-end: map-get($spacers, 2) solid $table-border-color;

    th {
      vertical-align: baseline;
      font-size: $si-font-size-title-2;
      font-weight: $si-font-weight-title-2;
      line-height: $si-line-height-title-2;
    }
  }

  tbody {
    th,
    td {
      block-size: 64px;
    }
  }

  // cancel out BS5 definition: we use background instead of box-shadow
  > :not(caption) > * > * {
    box-shadow: none;
    background-color: var(--bs-table-accent-bg);
  }
}

.table-sm tbody {
  th,
  td {
    block-size: 48px;
  }
}
