@import 'bootstrap/scss/navbar';

.navbar-nav {
  @include media-breakpoint-up(sm) {
    > .nav-item > .nav-link .badge {
      margin-block-start: -12px;
      margin-inline-start: -8px;
    }

    .quick-action.nav-item {
      margin-inline-start: map-get($spacers, 2);

      .dropdown-item {
        padding-inline: map-get($spacers, 2);

        .item-title,
        .menu-end-icon {
          display: none;
        }
      }
    }
  }

  .nav-link {
    display: flex;
    align-items: center;
    padding-inline: $navbar-nav-link-padding-x;
    padding-block: 10px;
    position: relative;
    cursor: pointer;

    .icon {
      margin-block: -5px;
    }
  }

  .dropdown-caret {
    font-size: 1.5rem;
    transition: transform $si-caret-transition-duration;
  }

  .show {
    > a > .dropdown-caret {
      transform: rotate(180deg);
    }
  }
}

.navbar-start {
  .nav-item {
    &.active .nav-link,
    &.show .nav-link {
      color: $element-ui-5;
      background: $element-action-primary;
    }
  }
}

.navbar-toggler {
  inline-size: $si-navbar-primary-height;
  block-size: $si-navbar-primary-height;
  background: $element-action-primary;
  color: $element-action-primary-text !important;  // stylelint-disable-line declaration-no-important
  font-size: $navbar-toggler-font-size;

  &:hover,
  &:focus,
  &:active {
    background: $element-action-primary-hover;
    box-shadow: none;
  }

  &:focus-visible {
    box-shadow: $si-focus-styling-inside-box-shadow;
    outline: none;
  }
}

.navbar-brand,
.navbar-app-title {
  display: flex;
  align-items: center;
  min-block-size: $si-navbar-primary-height;
}

.navbar-app-title {
  &.has-selected-item {
    @include media-breakpoint-down(sm) {
      display: none !important;  // stylelint-disable-line declaration-no-important
    }
  }

  &:hover,
  &:focus {
    text-decoration: none;
  }

  .navbar-app-title-inner {
    color: $element-text-secondary;
    line-height: 24px;

    @include media-breakpoint-up(lg) {
      border-inline-start: 1px solid $border-color;
    }
  }
}

.navbar-primary {
  background: $si-navbar-primary-bg;
  min-block-size: $si-navbar-primary-height;
  align-items: stretch;
  z-index: $zindex-fixed + 3 !important;  // stylelint-disable-line declaration-no-important

  @include media-breakpoint-up(sm) {
    .navbar-start,
    .navbar-end {
      display: flex;
    }

    .title-outside {
      display: none;
    }

    .navbar-end {
      margin-inline-start: 16px;
    }

    > *,
    .navbar-nav > *,
    .navbar-start > *,
    .navbar-end > * {
      display: flex;
      align-items: stretch;
    }

    .navbar-end {
      .navbar-nav > li > a {
        padding-block: 0;
        padding-inline: map-get($spacers, 4);

        .icon {
          font-size: $si-navbar-end-items-icon-size;
        }

        .item-title,
        .dropdown-caret {
          display: none;
        }
      }

      .dropdown-menu {
        inset-inline: auto map-get($spacers, 2) !important; // stylelint-disable-line declaration-no-important
      }
    }

    .navbar-start .dropdown.show .nav-item.show .dropdown-item {
      color: $element-text-secondary;
      background: $element-base-0;
    }
  }

  @include media-breakpoint-down(sm) {
    .navbar-collapse.show {
      max-block-size: calc(100vh - #{$si-navbar-primary-height});
      overflow-y: auto;
      padding-block: 8px;
      padding-inline: 0;
      box-shadow: $element-elevation-inset-1;
    }

    .nav-item > .nav-link,
    .dropdown-item {
      .badge,
      .badge-text + .item-title {
        margin-inline-start: map-get($spacers, 4);
      }

      .icon + .item-title {
        padding-inline-start: map-get($spacers, 5);
      }
    }

    .dropdown-item .item-title {
      flex: 0 0 auto;
    }
  }

  .nav-item {
    .dropdown-item {
      block-size: 48px;
      color: $element-text-secondary;
      font-weight: $si-font-weight-bold;
    }
  }

  .nav-item:not(.quick-action) {
    .dropdown-item {
      color: $element-text-primary;

      @include media-breakpoint-up(sm) {
        padding-block: 14px;
        padding-inline: map-get($spacers, 8);

        // repeat selector for higher specificity
        &.dropdown-item:hover {
          color: $element-action-primary-text !important; // stylelint-disable-line declaration-no-important
          background: $element-action-primary-hover !important; // stylelint-disable-line declaration-no-important
        }

        &.active {
          color: $element-action-primary-text !important; // stylelint-disable-line declaration-no-important
          background: $element-action-primary !important; // stylelint-disable-line declaration-no-important
        }

        &.show .dropdown-item {
          background: $element-action-primary !important; // stylelint-disable-line declaration-no-important
          color: $element-action-primary-text;
        }
      }

      > .icon + .item-title {
        @include media-breakpoint-down(sm) {
          padding-inline-start: map-get($spacers, 5);
        }
      }
    }
  }
}

.navbar-secondary {
  background: $si-navbar-secondary-bg;
}

.has-navbar-fixed-top {
  padding-block-start: $si-navbar-primary-height;
}

@include media-breakpoint-down(sm) {
  .navbar-nav .dropdown-menu {
    border: 0;
    box-shadow: none;
    background: transparent;

    .dropdown-divider {
      display: none;
    }
  }
}
