@import 'bootstrap/scss/buttons';

$btn-circle-font-size-default: 32px !default;
$btn-circle-font-size-sm: 24px !default;
$btn-circle-size-default: 40px !default;
$btn-circle-size-sm: 32px !default;
$btn-circle-size-xs: 24px !default;

// there are some non .btn buttons used in element
button {
  color: $element-text-primary;
}

@mixin element-button-variant($bg-gradient, $color, $hover-color) {
  &,
  &.dropdown-toggle {
    color: $color;
    background: $bg-gradient;
  }

  &:not(:disabled):not(.disabled) {
    &:active,
    &.active,
    &:hover,
    &.hover {
      background: $hover-color;
    }
  }
}

@mixin button-circle-size($btn-size, $font-size) {
  inline-size: $btn-size;
  block-size: $btn-size;
  font-size: $font-size;
}

$button-focus: make-focus($element-focus-default);

.btn {
  font-weight: $si-font-weight-title-2;
  display: inline-flex;
  align-items: center;
  justify-content: center;

  &:focus,
  &.focus {
    box-shadow: $button-focus !important; // stylelint-disable-line declaration-no-important
  }

  &.btn-info, // Note: design system has no "info" state for buttons
  &.btn-success, // Note: design system has no "success" state for buttons
  &.btn-primary {
    @include element-button-variant($element-action-primary, $element-action-primary-text, $element-action-primary-hover);
    border: 0;
  }

  &.btn-secondary {
    @include element-button-variant($element-action-secondary, $element-action-secondary-text, $element-action-secondary-hover);
    padding-block: ($btn-padding-y - 1px);
    padding-inline: ($btn-padding-x - 1px);
    border-color: $element-action-secondary-border;

    &:not(:disabled):not(.disabled) {
      &:active,
      &.active,
      &:hover {
        color: $element-action-secondary-text-hover;
        border-color: $element-action-secondary-border-hover;
      }
    }
  }

  &.btn-danger {
    @include element-button-variant($element-action-danger, $element-action-danger-text, $element-action-danger-hover);
    border: 0;
  }

  &.btn-warning {
    @include element-button-variant($element-action-warning, $element-action-warning-text,  $element-action-warning-hover);
    border: 0;
  }

  &.btn-tertiary,
  &.btn-link {
    @include element-button-variant(transparent, $element-text-active, $element-action-secondary-hover);
    border: 0;

    &:not(:disabled):not(.disabled) {
      &:active,
      &.active,
      &:hover {
        color: $element-ui-0-hover;
      }
    }
  }

  &:not(.btn-circle) {
    &:not(.btn-link) {
      min-inline-size: $btn-width-normal;
    }

    .icon {
      margin-block: -#{map-get($spacers, 2)};
      margin-inline: -#{map-get($spacers, 2)} #{map-get($spacers, 2)};
    }
  }
}

// Circle Buttons
// --------------
.btn-circle,
// Ensure .btn-circle is more specific than `form .btn`
form .btn.btn-circle {
  padding: 0 !important; // stylelint-disable-line declaration-no-important
  border-radius: 50%;
  flex-shrink: 0;

  &,
  &.btn-lg {
    @include button-circle-size($btn-circle-size-default, $btn-circle-font-size-default);
  }

  &.btn-sm {
    @include button-circle-size($btn-circle-size-sm, $btn-circle-font-size-sm);
  }

  &.btn-xs {
    @include button-circle-size($btn-circle-size-xs, $btn-circle-font-size-sm);
  }

  &.btn-ghost {
    @include element-button-variant(transparent, $element-ui-2, $element-action-secondary-hover);
    border: 0;
  }

  // deprecated, use .btn-tertiary, .btn-ghost
  &.btn-inline {
    @include element-button-variant($element-base-1, $element-action-secondary-text, $element-action-secondary-hover);
    border: 0;

    &:hover:not(:disabled):not(.disabled) {
      color: $element-action-secondary-text-hover;
    }

    &:active,
    &.active {
      background: $element-ui-4 !important; // stylelint-disable-line declaration-no-important
    }

    &.btn-danger:not(:disabled):not(.disabled) {
      &,
      &:active,
      &.active {
        color: $element-status-danger !important; // stylelint-disable-line declaration-no-important
      }
    }
  }
}

.btn-lg {
  line-height: 1.5;
}

.btn-sm {
  line-height: 1;
}

// this is for compatibility so that old markup looks ok-ish
.btn-close {
  @extend .btn;
  @extend .btn-circle;
  @extend .btn-sm;
  @extend .btn-ghost;

  font-weight: $si-font-weight-normal;

  &::before {
    content: '\002715';
    font-size: 1rem;
    margin-block-start: 2px;
  }

  > * {
    display: none !important; // stylelint-disable-line declaration-no-important
  }
}
