.in-electron {
  .collapse-toggle.mobile-drawer {
    margin-block-start: map-get($spacers, 4) + $si-titlebar-height !important; // stylelint-disable-line declaration-no-important
  }

  .si-layout-content-padding {
    padding-block-start: $si-titlebar-height + map-get($spacers, 6) !important; // stylelint-disable-line declaration-no-important
  }

  si-navbar-primary nav {
    inset-block-start: $si-titlebar-height !important; // stylelint-disable-line declaration-no-important
  }

  si-navbar-vertical nav {
    inset-block-start: $si-navbar-primary-height + $si-titlebar-height !important; // stylelint-disable-line declaration-no-important
  }

  si-navbar-vertical button {
    inset-block-start: $si-navbar-primary-height + $si-titlebar-height !important; // stylelint-disable-line declaration-no-important
  }

  si-side-panel > div:first-child {
    inset-block-start: $si-navbar-primary-height + $si-titlebar-height !important; // stylelint-disable-line declaration-no-important
  }

  si-landing-page {
    padding-block-start: $si-titlebar-height !important; // stylelint-disable-line declaration-no-important
  }
}
