@import 'bootstrap/scss/button-group';

.btn-group {
  .btn-check + .btn {
    background: transparent;
    color: $element-text-primary;
    border: 1px solid $element-ui-4;
    transition: none;
  }

  .btn-check:checked + .btn,
  .btn-check + .btn:hover {
    background: $element-action-secondary-hover;
    color: $element-action-secondary-text-hover;
    border-color: $element-action-secondary-border-hover;
  }

  .btn-check:focus +.btn {
    box-shadow: $si-focus-styling-box-shadow;
    z-index: 2;
  }
}
