// Unified focus styling

*:focus-visible:not(.focus-none),
*.focus-default:focus-visible,
*:focus-visible .focus-sub,
*:focus-visible > .focus-direct-sub {
  box-shadow: $si-focus-styling-box-shadow;
  outline: none;
}

*.focus-force:focus-visible {
  box-shadow: $si-focus-styling-box-shadow !important; // stylelint-disable-line declaration-no-important
}

*.focus-default:focus-visible {
  box-shadow: $si-focus-styling-box-shadow;
  outline: none;
}

*.focus-inside:focus-visible,
*:focus-visible .focus-sub-inside,
*:focus-visible > .focus-direct-sub-inside {
  box-shadow: $si-focus-styling-inside-box-shadow !important; // stylelint-disable-line declaration-no-important
}

// HACK: disable default focus to prevent WebKit issues
// See https://bugs.webkit.org/show_bug.cgi?id=185859
*[tabindex] {
  outline: none !important; // stylelint-disable-line declaration-no-important
}
