@import 'bootstrap/scss/popover';

$popover-header-bg: $element-ui-5;

.popover {
  min-block-size: 50px;
  min-inline-size: 176px;
  border-radius: $element-radius-2;
  border: 0;
}

.popover-header {
  display: flex;
  align-items: center;
  color: unset;
  border: 0;
  background: $popover-header-bg;
  padding-block-end: 0;

  + .popover-body {
    padding-block-start: map-get($spacers, 2);
  }
}

.bs-popover-bottom {
  .popover-header::before {
    border-block-end: $popover-border-width solid $popover-header-bg;
  }
}

.popover.bs-popover-top {
  margin-block-end: $popover-arrow-height !important; // stylelint-disable-line declaration-no-important
}

.popover.bs-popover-bottom {
  margin-block-start: $popover-arrow-height !important; // stylelint-disable-line declaration-no-important
}

.popover.bs-popover-start {
  margin-inline-end: $popover-arrow-height !important; // stylelint-disable-line declaration-no-important
}

.popover.bs-popover-end {
  margin-inline-start: $popover-arrow-height !important; // stylelint-disable-line declaration-no-important
}

.popover-start-center {
  @extend .bs-popover-end;

  .popover-arrow {
    inset-block-start: 50%;
    inset-inline-end: 100%;
    margin-block-start: -0.5 * $popover-arrow-width;
  }
}

.popover-end-center {
  @extend .bs-popover-start;

  .popover-arrow {
    inset-block-start: 50%;
    inset-inline-start: 100%;
    margin-block-start: -0.5 * $popover-arrow-width;
  }
}

.popover-center-bottom {
  @extend .bs-popover-top;

  .popover-arrow {
    inset-inline-start: 50%;
    margin-inline-start: -0.5 * $popover-arrow-width;
  }
}

.popover-center-top {
  @extend .bs-popover-bottom;

  .popover-arrow {
    inset-inline-start: 50%;
    margin-inline-start: -0.5 * $popover-arrow-width;
  }
}

.popover-start-bottom {
  @extend .bs-popover-top;
}

.popover-start-top {
  @extend .bs-popover-bottom;
}

.popover-end-bottom {
  @extend .bs-popover-top;
}

.popover-end-top {
  @extend .bs-popover-bottom;
}
