@mixin make-theme($vars, $name, $dark: false, $force-generate: false) {
  $theme-name: '';
  $mode: '';

  @if index($element-themes, $name) or $name == $element-theme-default or $force-generate {
    @if $name != $element-theme-default {
      $theme-name: '.theme-#{$name}';
    }

    @if $dark {
      $mode: '.app--dark';
    }

    :root#{$theme-name}#{$mode} {
      @each $token, $val in $vars {
        --element-#{$token}: #{$val};
      }
    }
  }
}
