@import 'bootstrap/scss/list-group';

.list-group {
  padding-inline-start: 0;
}

.list-group-item {
  outline: none;
  margin: 0;

  .list-group-item-heading {
    color: $headings-color;
  }

  &:first-child {
    @include border-top-radius($list-group-border-radius);
  }

  &:last-child {
    @include border-bottom-radius($list-group-border-radius);
  }
}

.list-header {
  font-size: $si-font-size-caption-1;
  padding-block: $list-group-item-padding-y;
  padding-inline: $list-group-item-padding-x;
  color: $headings-color;
  margin-block-end: -1px;
  border: 1px solid $list-group-border-color;
}

.list-group-item-action:focus-visible {
  box-shadow: $si-focus-styling-inside-box-shadow !important; /* stylelint-disable-line declaration-no-important */
}
