@import 'bootstrap/scss/utilities/api';

$text-colors: (
  'primary':     $element-text-active,
  'body':        $element-text-primary,
  'secondary':   $element-text-secondary,
  'tertiary':    $element-text-disabled,
  'success':     $element-text-success,
  'info':        $element-text-information,
  'warning':     $element-text-warning,
  'caution':     $element-text-caution,
  'danger':      $element-text-danger,
  'inverse':     $element-text-inverse,
) !default;

@each $color, $value in $text-colors {
  .text-#{$color} {
    color: #{$value} !important; // stylelint-disable-line declaration-no-important
  }
}

$background-colors: (
  'primary':            $element-ui-0,
  'secondary':          $element-text-secondary,
  'tertiary':           $element-text-disabled,
  'success':            $element-status-success,
  'info':               $element-status-information,
  'warning':            $element-status-warning,
  'danger':             $element-status-danger,
  'base-0':             $element-base-0,
  'base-1':             $element-base-1,
  'base-success':       $element-base-success,
  'base-info':          $element-base-information,
  'base-caution':       $element-base-caution,
  'base-warning':       $element-base-warning,
  'base-danger':        $element-base-danger,
  'base-translucent-1': $element-base-translucent-1,
  'base-translucent-2': $element-base-translucent-2
) !default;

@each $color, $value in $background-colors {
  .bg-#{$color} {
    background: #{$value} !important; // stylelint-disable-line declaration-no-important
  }
}

$status-colors: (
  'success':           $element-status-success,
  'info':              $element-status-information,
  'caution':           $element-status-caution,
  'warning':           $element-status-warning,
  'danger':            $element-status-danger,
  'critical':          $element-status-critical,
  'success-contrast':  $element-status-success-contrast,
  'info-contrast':     $element-status-information-contrast,
  'caution-contrast':  $element-status-caution-contrast,
  'warning-contrast':  $element-status-warning-contrast,
  'danger-contrast':   $element-status-danger-contrast,
  'critical-contrast': $element-status-critical-contrast,
) !default;

@each $color, $value in $status-colors {
  .status-#{$color} {
    color: #{$value} !important; // stylelint-disable-line declaration-no-important
  }
}
