@import 'bootstrap/scss/nav';

$nav-tabs-height: 40px;
$nav-tabs-min-width: 100px;

.nav {
  padding-inline-start: 0;

  .nav-link {
    &:hover {
      color: $element-action-secondary-text-hover;
      background: $element-action-secondary-hover;
      border-color: $element-action-secondary-border-hover;
    }
  }
}

.nav-tabs {
  position: relative;
  block-size: $nav-tabs-height;
  line-height: 1.125rem;
  border: 0;
  flex-wrap: nowrap;

  &::before {
    content: '';
    position: absolute;
    inset-inline: 0;
    inset-block-end: 0;
    block-size: $nav-tabs-border-width;
    background: $element-ui-4;
  }

  &:focus-visible::before {
    // because otherwise the focus is cut off
    background: $element-focus-default;
  }

  .nav-link {
    display: flex;
    align-items: center;
    justify-content: center;
    block-size: $nav-tabs-height;
    min-inline-size: $nav-tabs-min-width;
    border-width: 0 0 $nav-tabs-border-width;
    cursor: pointer;

    .badge {
      margin-inline-start: map-get($spacers, 2);
      margin-block-start: -7px;
    }

    &.active {
      border-color: $element-action-secondary-border-hover;
    }
  }
}
