@function make-focus($focus-color) {
  $focus-width: calc($element-button-focus-width + $element-button-focus-overlay-width);
  $focus-overlay: 0 0 0 $element-button-focus-overlay-width $element-button-focus-overlay-color;

  @return $focus-overlay, 0 0 0 $focus-width $focus-color;
}

// same as make-focus, but with an additional offset for things that use box-shadow as border
// e.g. the textarea
@function make-focus-with-offset($focus-color, $offset) {
  $focus-width: calc($element-button-focus-width + $element-button-focus-overlay-width + $offset);
  $focus-overlay: 0 0 0 calc($element-button-focus-overlay-width + $offset) $element-button-focus-overlay-color;

  @return $focus-overlay, 0 0 0 $focus-width $focus-color;
}

@function make-focus-inside($focus-color) {
  $focus-width: calc($element-button-focus-width + $element-button-focus-overlay-width);
  $focus-inside-overlay: inset 0 0 0 $focus-width $element-button-focus-overlay-color;

  @return inset 0 0 0 $element-button-focus-width $focus-color, $focus-inside-overlay;
}

$si-focus-styling-box-shadow: make-focus($element-focus-default);
$si-focus-styling-inside-box-shadow: make-focus-inside($element-focus-default);
