// Override/enhance the form validation state mixin to also account for class
// names provided by Angulars validation.
//
// Note: the '.was-validated' selector used by Bootstrap is supposed to live
// on the wrapping '<form>' element, so decide if this should be changed to
// an appropriate Angular class that may get applied to a form after save.
@mixin form-validation-state-selector($state) {
  @if ($state == 'valid' or $state == 'invalid') {
    .was-validated &:#{$state},
    &:not(:disabled):not([readonly]).ng-touched.ng-#{$state},
    &.is-#{$state} {
      @content;
    }
  } @else {
    &:not(:disabled):not([readonly]):not(:read-only).ng-touched.ng-#{$state},
    &.is-#{$state} {
      @content;
    }
  }
}

%state-feedback {
  @include font-size($form-feedback-font-size);
  display: none;
  inline-size: 100%;
  margin-block-start: $form-feedback-margin-top;
}

%state-icon {
  display: none !important; // stylelint-disable-line declaration-no-important
  position: absolute;
  inset-block-start: map-get($spacers, 4);
  inset-inline-end: $input-padding-x;

  &,
  .icon {
    font-size: 1rem;
  }
}

@mixin form-validation-state-enh($state, $color, $icon, $icon-color, $text-color, $focus-color) {
  .#{$state}-feedback {
    @extend %state-feedback;
    color: $text-color;
  }

  .#{$state}-icon {
    @extend %state-icon;
    color: $icon-color;
  }

  .form-control {
    @include form-validation-state-selector($state) {
      @if $color {
        &:not(.no-validation):not(:hover),
        &:not(.no-validation):focus {
          --border-color: #{$color};
        }
      }

      ~ .#{$state}-feedback,
      ~ .#{$state}-icon {
        display: flex !important; // stylelint-disable-line declaration-no-important
      }
    }
  }

  select {
    ~ .#{$state}-icon {
      inset-inline-end: 4 * $input-padding-x;
    }
  }

  .form-control-file {
    .was-validated &:#{$state},
    &.is-#{$state},
    // custom Angular specific selectors
    &:required:not(:disabled):not([readonly]):not(:read-only).ng-touched.ng-#{$state} {
      ~ .#{$state}-feedback {
        display: block;
      }
    }
  }

  .form-check-input {
    .was-validated &:#{$state},
    &.is-#{$state},
    // custom Angular specific selectors
    &:required:not(:disabled):not([readonly]):not(:read-only).ng-touched.ng-#{$state} {
      ~ .form-check-label {
        color: $text-color;
      }

      ~ .#{$state}-feedback {
        display: block;
      }
    }
  }
}
