$font-path: '../fonts' !default;
$simpl-fonts-no-woff: false !default;

@function default-font-types() {
  @if $simpl-fonts-no-woff {
    @return (local, otf, ttf);
  } @else {
    @return (local, woff, woff2);
  }
}
$simpl-font-types: default-font-types() !default;

@function make-font-url($map) {
  $font-list: ();
  @each $type, $val in $map {
    @if index($simpl-font-types, $type) {
      $font-list: append($font-list, $val, $separator: comma);
    }
  }
  @return $font-list;
}

@at-root {
  $font-types: (
    woff2: url('#{$font-path}/simpl-element-icons.woff2') format('woff2'),
    woff: url('#{$font-path}/simpl-element-icons.woff') format('woff'),
    ttf: url('#{$font-path}/simpl-element-icons.ttf') format('truetype'),
  );
  @font-face {
    font-family: 'simpl-element-icons';
    src: make-font-url($font-types);
    font-weight: normal;
    font-style: normal;
  }
}

[class^='element-'],
[class*=' element-'] {
  display: inline-block;
  font-family: 'simpl-element-icons';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: middle;
  -webkit-font-smoothing: antialiased; // sass-lint:disable-line no-vendor-prefixes
  -moz-osx-font-smoothing: grayscale; // sass-lint:disable-line no-vendor-prefixes
  // this ensures icons have the correct dimensions even before the icon is loaded and rendered
  // and thus prevents a layout shift
  min-width: 1em;
}

