@import 'bootstrap/scss/forms/labels';
@import 'bootstrap/scss/forms/form-text';
@import 'bootstrap/scss/forms/form-control';
// not used, we mostly extend .form-control
// @import 'bootstrap/scss/forms/form-select';
@import 'form-check';
@import 'bootstrap/scss/forms/form-range';
// not part of the design system
// @import 'bootstrap/scss/forms/floating-labels';
@import 'bootstrap/scss/forms/input-group';
// not used since we have our own
// @import 'bootstrap/scss/forms/validation';

@each $state, $data in $form-validation-states {
  @include form-validation-state-enh($state, map-get($data, color), map-get($data, icon), map-get($data, icon-color), map-get($data, text-color), map-get($data, focus-color));
}

.form-control {
  --border-color: #{$element-ui-2};
  border-color: var(--border-color) !important; // stylelint-disable-line declaration-no-important

  &:focus,
  &:hover {
    --border-color: #{$element-ui-1};
  }

  &.readonly,
  &[readonly] {
    &,
    &:hover,
    &:focus {
      --border-color: #{$element-ui-4};
      background-color: $input-bg;
    }
  }

  &.disabled,
  &:disabled {
    &,
    &:hover,
    &:focus {
      --border-color: #{$element-ui-3};
      color: $element-text-disabled;
    }
  }

  &:focus {
    box-shadow: make-focus($element-focus-default);
  }
}

textarea.form-control {
  padding-block: $input-padding-y;
  line-height: 1.5;
  // prevent scrollbar overlapping rounded borders
  box-shadow: 0 0 0 1px var(--border-color);
  border: 3px solid $input-bg !important; // stylelint-disable-line declaration-no-important

  &:focus {
    box-shadow: 0 0 0 1px var(--border-color), make-focus-with-offset($element-focus-default, 1px);
  }
}

select.form-control:not([multiple]) {
  padding-block: $input-padding-y;
  padding-inline: map-get($spacers, 4) map-get($spacers, 8);
  appearance: none;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'><path d='M352.79 203.21a12.45 12.45 0 011.44 15.86l-1.44 1.72-88 88a12.45 12.45 0 01-15.86 1.44l-1.72-1.44-88-88a12.43 12.43 0 0115.86-19l1.72 1.44L256 282.42l79.21-79.21a12.45 12.45 0 0115.86-1.44z'/></svg>");
  background-repeat: no-repeat;
  background-position: right 2px top 50%;
  background-size: 1.5rem;

  @include rtl {
    background-position: left 2px top 50%;
  }

  .app--dark & {
    background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'><path fill='white' d='M352.79 203.21a12.45 12.45 0 011.44 15.86l-1.44 1.72-88 88a12.45 12.45 0 01-15.86 1.44l-1.72-1.44-88-88a12.43 12.43 0 0115.86-19l1.72 1.44L256 282.42l79.21-79.21a12.45 12.45 0 0115.86-1.44z'/></svg>");
  }
}

.form-label {
  padding-inline-start: $input-padding-x;
  display: inline-block;
}

.col-form-label {
  font-size: $form-label-font-size;
  padding-block: map-get($spacers, 4) - 1px 0;
}

.required:is(.form-label, .col-form-label)::before {
  content: '*';
}

.form-text {
  display: block;
}

.form-control-has-icon {
  position: relative;

  .form-control {
    padding-inline-end: 2em;
  }
}

.text-end .form-label {
  padding-inline-end: $input-padding-x;
}

select.form-control[multiple] {
  border-start-end-radius: 0;
  border-end-end-radius: 0;
  padding: 0;

  option {
    padding: map-get($spacers, 4);
  }

  option:checked,
  &:not(:disabled) option:not(:disabled):hover {
    background: $element-base-0;
    color: $element-text-active;
  }
}

.form-select {
  @extend .form-control;
}

