@import 'bootstrap/scss/card';

.card-header {
  font-size: $si-font-size-title-2;
  font-weight: $si-font-weight-title-2;
  border-block-end: 0;
  min-block-size: map-get($spacers, 9) + map-get($spacers, 4);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;

  &,
  .icon {
    line-height: $si-font-size-title-2;
  }
}

.card-body {
  overflow: hidden;
}

// If the card has a header, remove the top padding for the body.
// Since we removed the separator line from the header, too much
// padding looks weird.
.card-header + .card-body {
  padding-block-start: 0;
}
