$datatable-header-border-color: $table-border-color !default;
$datatable-header-background-color: transparent !default;
$datatable-header-color: $element-text-primary !default;
$datatable-footer-background-color: transparent !default;

$datatable-header-height: 40px !default;
$datatable-cell-min-width: 40px !default;
$datatable-body-row-min-height: 48px !default;

$datatable-ghost-cell-container-background: $table-bg !default;

$datatable-ghost-cell-strip-background: $element-base-0 !default;
$datatable-ghost-cell-strip-background-image: linear-gradient(to right, $element-base-0 0%, $element-ui-4 20%, $element-base-0 40%, $element-base-0 100%) !default;
$datatable-ghost-cell-strip-radius: $element-radius-2 !default;
$datatble-ghost-cell-animation-duration: element-transition-duration(10s) !default;

.ngx-datatable.table-element {
  background-color: $table-bg;
  border-radius: $element-radius-2;

  &.table-custom {
    --si-datatable-row-min-height: 24px;
  }

  // any kind of selection: apply a row hover state
  &.single-selection,
  &.multi-selection,
  &.multi-click-selection,
  &.cell-selection,
  &.checkbox-selection {
    .datatable-row-wrapper {
      &:hover {
        background-color: $table-hover-bg;
      }
    }
  }

  // any selection type but 'cell'
  &.single-selection,
  &.multi-selection,
  &.multi-click-selection,
  &.checkbox-selection {
    .datatable-body-row {
      &.active {
        &,
        .datatable-row-group {
          background-color: $table-active-bg;
        }
      }
    }
  }

  // solely 'cell' selection type
  &.cell-selection {
    .datatable-body-cell {
      &.active,
      &.active .datatable-row-group,
      &:focus,
      &:focus .datatable-row-group {
        background-color: $table-active-bg;
      }
    }
  }

  // not 'cell' selection type
  &:not(.cell-selection) {
    .datatable-body-row {
      &:focus,
      &:focus .datatable-row-group {
        background-color: $table-active-bg;
      }
    }
  }

  // 'checkbox' selection
  .datatable-header-cell,
  .datatable-body-cell {
    label.datatable-checkbox {
      display: inline-flex;
      inline-size: 13px;
      block-size: 13px;
      margin-block: 0;
      margin-inline: 0 map-get($spacers, 6);
      position: relative;
      inset-block-start: 2px;

      > input {
        inline-size: 100%;
        block-size: 100%;
      }
    }
  }

  // Global Row Styles
  .datatable-header,
  .datatable-body {
    ghost-loader:not(.ghost-overlay) {
      display: flex;
      flex: 1 1 0;

      .ghost-cell-strip,
      .ghost-element,
      .ghost-loader {
        display: flex;
        flex: 1 1 0;
        inline-size: unset !important; // stylelint-disable-line declaration-no-important
      }
    }

    .datatable-header-cell,
    .datatable-body-cell {
      display: flex;
      align-items: center;
      padding-block: 0;
      padding-inline: 4px;
      min-inline-size: $datatable-cell-min-width;
      line-height: $si-line-height-body-2;

      > div {
        padding: 4px;
      }

      &:first-child {
        padding-inline-start: map-get($spacers, 5);
      }

      &:last-child {
        padding-inline-end: map-get($spacers, 5);
      }
    }
  }

  // Header Styles
  .datatable-header {
    padding: 0;
    display: flex;
    align-items: center;
    border-block-end: 4px solid $datatable-header-border-color;
    background-color: $datatable-header-background-color;
    color: $datatable-header-color;
    block-size: $datatable-header-height;
    vertical-align: baseline;
    font-size: $si-font-size-title-2;
    font-weight: $si-font-weight-title-2;
    line-height: $si-line-height-title-2;
    user-select: none;

    .sort-btn {
      color: $datatable-header-color !important; // stylelint-disable-line declaration-no-important
    }

    // Resizable handle
    .resizeable:hover {
      .resize-handle {
        background-color: $element-base-0;
        inline-size: 2px;
        border-radius: 2px;
        padding-block: 0;
        padding-inline: 2px;
      }
    }

    .datatable-row-left,
    .datatable-row-right {
      background-color: $table-bg;
      display: flex;
    }
  }

  .datatable-scroll {
    inline-size: unset !important; // stylelint-disable-line declaration-no-important
  }

  // Body Styles
  .datatable-body {
    .datatable-row-wrapper {
      min-block-size: var(--si-datatable-row-min-height, #{$datatable-body-row-min-height});
      justify-content: center;
      padding: 0;
      inline-size: 100%;
    }

    .datatable-body-row {
      border-block-end: 1px solid $datatable-header-border-color;
      inline-size: 100% !important; // stylelint-disable-line declaration-no-important
      min-inline-size: fit-content;

      .datatable-body-cell-label {
        overflow: hidden;
        text-overflow: ellipsis;
      }

      &:hover {
        &:not(.active) {
          background-color: $table-hover-bg;

          .datatable-row-group {
            background-color: $element-base-0;
          }
        }
      }

      .datatable-row-group {
        background-color: $table-bg;
      }
    }

    .datatable-body-cell,
    .datatable-body-row {
      &:focus-visible {
        position: relative;
        box-shadow: $si-focus-styling-inside-box-shadow;

        &::after {
          position: absolute;
          content: '';
          inset: 0;
          z-index: 9;
          box-shadow: $si-focus-styling-inside-box-shadow;
          pointer-events: none;
        }
      }
    }
  }

  // Footer Styles
  .datatable-footer {
    border-block-start: 1px solid $element-ui-4;
    background-color: $datatable-footer-background-color;
    color: $element-text-secondary;
    padding-inline: map-get($spacers, 6);
    font-size: $si-font-size-caption-1;
  }

  // Tree view icon styles
  .datatable-tree-button {
    border: 0;
    background: none;
    margin-inline: map-get($spacers, 2);
    padding: 0;

    &[disabled] {
      display: none;
    }

    &:focus-visible {
      box-shadow: $si-focus-styling-inside-box-shadow !important; // stylelint-disable-line declaration-no-important
    }
  }

  .datatable-tree-button span i {
    @extend .element-right-2 !optional;
    @extend [class^='element-'] !optional;

    @include rtl {
      transform: scaleX(-1);
    }
  }

  .datatable-tree-button span i.datatable-icon-down {
    @extend .element-down-2 !optional;
  }
}
