@import 'bootstrap/scss/modal';

.modal-dialog {
  // because of :focus-visible that shouldn't be visible
  box-shadow: none !important; // stylelint-disable-line declaration-no-important
}

.modal-header .btn-close {
  margin: 0;
}

.modal-title {
  font-size: $si-font-size-title-1;
  font-weight: $si-font-weight-title-1;
}

.modal-body {
  padding-block: map-get($spacers, 5) $modal-inner-padding;
  // use margin instead of padding so that the scrollbar isn't on the edge of the dialog
  margin-inline: $modal-inner-padding;
  padding-inline: 0;

  .modal-dialog-scrollable & {
    padding-block: 0;
  }
}

.si-modal-content-icon-wrapper {
  padding-block-start: $modal-header-padding-y;
  padding-inline-start: $modal-inner-padding;
  display: flex;
}

.si-modal-content-icon-spacer {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-block-start: $modal-header-padding-y * -1;
  margin-inline-start: map-get($spacers, 6) - $modal-inner-padding;
}
