// Variables
$font-path: '../fonts' !default;
$siemens-font-path: $font-path !default;
$simpl-fonts-no-woff: false !default;

@function default-font-types() {
  @if $simpl-fonts-no-woff {
    @return (local, otf, ttf);
  } @else {
    @return (local, woff, woff2);
  }
}
$simpl-font-types: default-font-types() !default;

@function make-font-url($map) {
  $font-list: ();
  @each $type, $val in $map {
    @if index($simpl-font-types, $type) {
      $font-list: append($font-list, $val, $separator: comma);
    }
  }
  @return $font-list;
}

// Font Face mixin
//
// This mixin simplifies proper @font-face definitions.
@mixin font-face($family-name, $family-font, $weight, $name, $italic-name: #{$name}Italic) {
  $font-types-normal: (
    local: local('#{$family-name} #{$name}'),
    otf: url('#{$siemens-font-path}/#{$family-font}_#{$name}.otf') format('opentype'),
    woff2: url('#{$siemens-font-path}/#{$family-font}_#{$name}.woff2') format('woff2'),
    woff: url('#{$siemens-font-path}/#{$family-font}_#{$name}.woff') format('woff'),
  );
  $font-types-italic: (
    local: local('#{$family-name $name} Italic'),
    otf: url('#{$siemens-font-path}/#{$family-font}_#{$italic-name}.otf') format('opentype'),
    woff2: url('#{$siemens-font-path}/#{$family-font}_#{$italic-name}.woff2') format('woff2'),
    woff: url('#{$siemens-font-path}/#{$family-font}_#{$italic-name}.woff') format('woff'),
  );

  @at-root {
    @font-face {
      font-family: $family-name;
      font-weight: $weight;
      font-style: normal;
      src: make-font-url($font-types-normal);
    }

    @font-face {
      font-family: $family-name;
      font-weight: $weight;
      font-style: italic;
      src: make-font-url($font-types-italic);
    }
  }
}
