$utilities: (
  'start': (
    property: inset-inline-start,
    class: start,
    values: $position-values
  ),
  'end': (
    property: inset-inline-end,
    class: end,
    values: $position-values
  ),
  'border-end': (
    property: border-inline-end,
    class: border-end,
    values: (
      null: $border-width solid $border-color,
      0: 0,
    )
  ),
  'border-start': (
    property: border-inline-start,
    class: border-start,
    values: (
      null: $border-width solid $border-color,
      0: 0,
    )
  ),
  'margin-x': (
    responsive: true,
    property: margin-inline,
    class: mx,
    values: map-merge($spacers, (auto: auto))
  ),
  'margin-end': (
    responsive: true,
    property: margin-inline-end,
    class: me,
    values: map-merge($spacers, (auto: auto))
  ),
  'margin-start': (
    responsive: true,
    property: margin-inline-start,
    class: ms,
    values: map-merge($spacers, (auto: auto))
  ),
  'negative-margin-x': (
    responsive: true,
    property: margin-inline,
    class: mx,
    values: $negative-spacers
  ),
  'negative-margin-end': (
    responsive: true,
    property: margin-inline-end,
    class: me,
    values: $negative-spacers
  ),
  'negative-margin-start': (
    responsive: true,
    property: margin-inline-start,
    class: ms,
    values: $negative-spacers
  ),
  'padding-x': (
    responsive: true,
    property: padding-inline,
    class: px,
    values: $spacers
  ),
  'padding-end': (
    responsive: true,
    property: padding-inline-end,
    class: pe,
    values: $spacers
  ),
  'padding-start': (
    responsive: true,
    property: padding-inline-start,
    class: ps,
    values: $spacers
  ),
  'text-align': (
    responsive: true,
    property: text-align,
    class: text,
    values: (
      start: start,
      end: end,
      center: center,
    )
  ),
  'rounded-end': (
    property: border-end-start-radius border-end-end-radius,
    class: rounded-end,
    values: (null: $border-radius)
  ),
  'rounded-start': (
    property: border-start-start-radius border-end-start-radius,
    class: rounded-start,
    values: (null: $border-radius)
  )
);

// let the rest be plain BS
@import 'bootstrap/scss/_utilities';  // stylelint-disable-line scss/at-import-no-partial-leading-underscore

@mixin rtl {
  // for WebKit/Blink based browsers, not (yet) supporting :dir
  [dir=rtl] #{if(&, "&","")} {
    @content;
  }

  // Spec and Firefox
  :dir(rtl) #{if(&, "&","")} {
    @content;
  }
}
