@import 'bootstrap/scss/dropdown';

.dropdown-header {
  font-size: $dropdown-font-size;
  font-weight: $si-font-weight-bold;
}

.dropdown-divider {
  margin-inline: map-get($spacers, 5);
}

.dropdown-menu {
  margin-block-start: map-get($spacers, 1);
  text-align: start;
}

.dropdown-menu,
.dropdown-item {
  .icon + .item-title {
    padding-inline-start: map-get($spacers, 4);
  }
}

.dropdown-toggle {
  --caret-rotate: 0deg;
  text-align: start;

  > .dropdown-caret {
    padding-inline-start: map-get($spacers, 2);
    margin-block: -#{map-get($spacers, 2)} !important; // stylelint-disable-line declaration-no-important
    margin-inline: auto -#{map-get($spacers, 4)} !important; // stylelint-disable-line declaration-no-important
    font-size: 1.5rem !important; // stylelint-disable-line declaration-no-important

    &::before {
      transform: rotate(var(--caret-rotate));
      transition: transform $si-caret-transition-duration;
      display: inline-block;
    }
  }

  &.show > .dropdown-caret::before {
    transform: rotate(calc(var(--caret-rotate) + 180deg));
  }
}

.dropup .dropdown-toggle {
  --caret-rotate: 180deg;
}

.dropstart .dropdown-toggle {
  --caret-rotate: 90deg;
}

.dropend .dropdown-toggle {
  --caret-rotate: 270deg;
}

.dropdown-item {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  block-size: 32px;

  .item-title {
    flex: 1;
  }

  &:focus {
    background: transparent;
  }

  &:focus:not(:focus-visible) {
    &:hover {
      background-color: $dropdown-link-hover-bg;
    }
  }

  &.active:focus:not(:focus-visible) {
    outline: none;
    box-shadow: none;
  }

  .menu-end-icon {
    margin-inline-end: -1 * map-get($spacers, 3);

    &.element-record-filled {
      font-size: 1rem;
      margin-inline-end: -1 * map-get($spacers, 1);
    }
  }
}

.dropdown-item-static {
  &.active,
  &:active,
  &:hover,
  &:hover:focus:not(:focus-visible),
  &:focus {
    background-color: $element-base-1;
  }
}

.dropdown > .dropdown-menu {
  inset-block: 100% auto;
  inset-inline: 0 auto;
  margin-block: $dropdown-spacer 0;
  margin-inline: 0;
}

.dropup > .dropdown-menu {
  inset-block: auto 100%;
  inset-inline: auto;
  margin-block: 0 $dropdown-spacer;
  margin-inline: 0;
}

.dropend > .dropdown-menu {
  inset-block: 0 auto;
  inset-inline: 100% auto;
  margin-block: 0;
  margin-inline: $dropdown-spacer 0;
}

.dropstart > .dropdown-menu {
  inset-block: 0 auto;
  inset-inline: auto 100%;
  margin-block: 0;
  margin-inline: 0 $dropdown-spacer;
}

.dropdown-menu {
  &.align-center {
    left: 0; // stylelint-disable-line liberty/use-logical-spec
    right: unset; // stylelint-disable-line liberty/use-logical-spec
    transform: translateX(-50%);
  }

  &.align-start {
    inset-inline: 0 auto;
  }

  &.align-end {
    inset-inline: auto 0;
  }
}
