@import 'bootstrap/scss/alert';

.alert {
  overflow: hidden;
  font-size: $si-font-size-body-1;
  line-height: 18px;

  &::before {
    content: '';
    inset-block-start: 0;
    inset-inline-start: 0;
    position: absolute;
    inline-size: 4px;
    block-size: 100%;
    background: var(--color-bar);
  }
}

.alert-success {
  --color-bar: #{$element-status-success};
  color: $element-text-success;
  background: $element-base-success;
}

.alert-warning {
  --color-bar: #{$element-status-warning};
  color: $element-text-warning;
  background: $element-base-warning;
}

.alert-danger {
  --color-bar: #{$element-status-danger};
  color: $element-text-danger;
  background: $element-base-danger;
}

.alert-info {
  --color-bar: #{$element-status-information};
  color: $element-text-information;
  background: $element-base-information;
}
