@import 'bootstrap/scss/type';

h1,
.h1,
.si-h1 {
  @include si-font($si-font-size-h1, $si-line-height-h1, $si-font-weight-h1);
}

.si-h1-black {
  @include si-font($si-font-size-h1-black, $si-line-height-h1-black, $si-font-weight-h1-black);
}

h2,
.h2,
.si-h2 {
  @include si-font($si-font-size-h2, $si-line-height-h2, $si-font-weight-h2);
}

h3,
.h3,
.si-h3 {
  @include si-font($si-font-size-h3, $si-line-height-h3, $si-font-weight-h3);
}

h4,
h5,
h6,
.h4,
.h5,
.h6 {
  @include si-font($si-font-size-body-1, $si-line-height-body-1, $si-font-weight-body-1);
}

.si-title-1 {
  @include si-font($si-font-size-title-1, $si-line-height-title-1, $si-font-weight-title-1);
}

.si-body-1 {
  @include si-font($si-font-size-body-1, $si-line-height-body-1, $si-font-weight-body-1);
}

.si-title-2 {
  @include si-font($si-font-size-title-2, $si-line-height-title-2, $si-font-weight-title-2);
}

.si-body-2 {
  @include si-font($si-font-size-body-2, $si-line-height-body-2, $si-font-weight-body-2);
}

.si-caption {
  @include si-font($si-font-size-caption-1, $si-line-height-caption-1, $si-font-weight-caption-1);
}

.text-pre {
  white-space: pre;
}

.text-pre-wrap {
  white-space: pre-wrap;
}
