// stylelint-disable declaration-no-important
@import 'bootstrap/scss/badge';

.badge {
  color: $siemens-black;
  margin-block: 0;
  margin-inline: map-get($spacers, 4);
  block-size: map-get($spacers, 8);
  line-height: map-get($spacers, 8);
  font-size: $si-font-size-title-2;
  font-weight: $si-font-weight-title-2;
  max-inline-size: 196px;
  text-overflow: ellipsis;
  overflow: hidden;

  &.bg-default {
    background-color: $element-base-0 !important;
    color: $element-text-primary;
  }

  &.bg-primary {
    background-color: $element-ui-0 !important;
    color: $element-text-inverse;
  }

  &.bg-secondary {
    background-color: $element-ui-4 !important;
    color: $element-text-primary;
  }

  &.bg-info {
    background-color: $element-base-information !important;
    color: $element-text-information;
  }

  &.bg-success {
    background-color: $element-base-success !important;
    color: $element-text-success;
  }

  &.bg-caution {
    background-color: $element-base-caution !important;
    color: $element-text-caution;
  }

  &.bg-warning {
    background-color: $element-base-warning !important;
    color: $element-text-warning;
  }

  &.bg-danger {
    background-color: $element-base-danger !important;
    color: $element-text-danger;
  }

  &.bg-inverse {
    background-color: $element-ui-1 !important;
    color: $element-text-inverse;
  }
}

.badge-dot {
  position: relative;

  &::after {
    content: '';
    inline-size: 10px;
    block-size: 10px;
    border-radius: 5px;
    position: absolute;
    inset-block-start: 0;
    inset-inline-end: 0;
    background-color: $element-status-danger;
  }
}

.icon + .badge-text {
  block-size: 14px;
  min-inline-size: 14px;
  border-radius: 7px;
  position: relative;
  inset-block-start: -5px;
  inset-inline-start: -12px;
  margin-inline-end: -12px;
  background-color: $element-status-danger;
  color: $siemens-white;
  font-size: $si-font-size-caption-1;
  line-height: 13px;
  padding-block: 0;
  padding-inline: map-get($spacers, 2);
  font-family: $font-family-sans-serif;
  font-weight: $si-font-weight-bold;
}
