@import 'bootstrap/scss/tooltip';

.tooltip-inner {
  min-inline-size: 100px;
}

.tooltip-start-center {
  @extend .bs-tooltip-end;

  .tooltip-arrow {
    inset-block-start: 50%;
    inset-inline-end: 100%;
    margin-block-start: $tooltip-arrow-width * -0.5;
  }
}

.tooltip-end-center {
  @extend .bs-tooltip-start;

  .tooltip-arrow {
    inset-block-start: 50%;
    inset-inline-start: 100%;
    margin-block-start: $tooltip-arrow-width * -0.5;
    margin-inline-start: $tooltip-arrow-height * -1;
  }
}

.tooltip-center-bottom {
  @extend .bs-tooltip-top;

  .tooltip-arrow {
    inset-inline-start: 50%;
    margin-inline-start: $tooltip-arrow-width * -0.5;
  }
}

.tooltip-center-top {
  @extend .bs-tooltip-bottom;

  .tooltip-arrow {
    inset-inline-start: 50%;
    margin-inline-start: $tooltip-arrow-width * -0.5;
  }
}

.tooltip-start-bottom {
  @extend .bs-tooltip-top;
}

.tooltip-start-top {
  @extend .bs-tooltip-bottom;
}

.tooltip-end-bottom {
  @extend .bs-tooltip-top;
}

.tooltip-end-top {
  @extend .bs-tooltip-bottom;
}
