// Get all the SCSS variables
@import 'styles/all-variables';

// Import Siemens fonts
$siemens-font-path: '~@simpl/fonts/dist/fonts' !default;
@import '@simpl/fonts/src/styles/siemens-sans';

// Load Bootstrap
@import 'styles/bootstrap';
@import 'styles/theme/themes';

// Component extensions
@import 'styles/components/container-grid';
@import 'styles/components/datatable';
@import 'styles/components/elevation';
@import 'styles/components/icons';
@import 'styles/components/layout';
@import 'styles/components/links';
@import 'styles/components/focus';
@import 'styles/components/electron-titlebar';
@import 'styles/components/switch';

// accessibility extensions
@import 'styles/accessibility';

html,
body {
  block-size: 100%;
}

// Define what 1rem actually means. Everything here assumes 16px.
// When the whole thing is properly done, carefully mixing (r)em and px values,
// this can be removed to allow the user's setting.
html {
  font-size: 16px;
}
