// the minimal set of variables w/o all the heavy bootstrap stuff

@import 'variables/animations';
@import 'variables/semantic-tokens';
@import 'variables/typography';
@import 'variables/elevation';
@import 'variables/focus';
@import 'variables/spacers';
@import 'variables/zindex';
@import 'variables/layout';

// Custom variables
$si-caret-transition-duration: element-transition-duration(0.4s);
$si-icon-font-size: 1.5rem;
$si-icon-font-size-small: 1rem;

$btn-width-normal: 100px;

$si-list-group-item-focus-background-color: $element-ui-5;

$si-input-padding-x: 0.5rem;
$si-input-padding-y: (map-get($spacers, 3) - 1px);

$si-link-default: $element-ui-0;
$si-link-active: $element-ui-0-hover;

// Component styling
$si-navbar-primary-bg: $element-base-1;
$si-navbar-image-size: $si-icon-font-size;
$si-navbar-icon-size: $si-icon-font-size;
$si-navbar-end-items-icon-size: $si-icon-font-size;
$si-navbar-avatar-size: 2rem;
$si-navbar-image-color: $element-ui-2;
$si-navbar-image-background-color: $element-base-0;
$si-navbar-primary-height: 48px;

$si-navbar-secondary-bg: $element-base-1;
$si-navbar-secondary-height: 40px;

$si-titlebar-height: 36px;
