// ----------------------------------------------------------------------------
// this is a drop-in replacement to bootstrap's _form-check.scss
// there's too many differences between what BS5 does and what the design system expects

$form-check-size: map-get($spacers, 6);
$form-radio-size: map-get($spacers, 6) + 2px;
$form-radio-inner-size: map-get($spacers, 3);
$form-radio-offset: ($form-radio-size - $form-radio-inner-size) * 0.5 - 1px;
$form-check-padding-x: map-get($spacers, 2);
$form-check-padding-y: map-get($spacers, 1);

$form-check-label-start: $form-check-size + map-get($spacers, 4) + $form-check-padding-x;


.form-check {
  .valid-feedback,
  .invalid-feedback {
    padding-inline-start: $form-check-label-start;
  }
}

.form-check-inline {
  display: inline-block;
  margin-inline-end: $form-check-inline-margin-end;
}

%form-check-base {
  position: relative;
  appearance: none;
  background-color: $element-ui-5;
  border: 1px solid $element-ui-1;
  outline: 0;
  flex-shrink: 0;

  &[disabled] {
    border-color: $element-ui-3;
  }

  &:not([disabled]) {
    &:hover {
      border-color: $element-action-secondary-border-hover;
      background: $element-action-secondary-hover;
    }
  }
}

%form-check-checkbox {
  inline-size: $form-check-size !important; // stylelint-disable-line declaration-no-important
  block-size: $form-check-size !important; // stylelint-disable-line declaration-no-important
  border-radius: calc($element-input-radius - $element-radius-1);
  inset-inline-start: map-get($spacers, 1);
  inset-block-start: map-get($spacers, 1) + 1px;
  margin-inline-end: map-get($spacers, 1);

  &::after {
    content: '';
    position: absolute;
    display: block;
    inset-inline-start: -1px;
    inset-block-start: -1px;
    inline-size: $form-check-size;
    block-size: $form-check-size;
    mask-image: escape-svg($form-check-input-checked-bg-image);
    opacity: 0;
  }

  &:checked {
    &:not([disabled]) {
      background-color: $element-ui-0;
      border-color: $element-ui-0;

      &:hover {
        background-color: $element-ui-0-hover;
        border-color: $element-ui-0-hover;
      }
    }

    &[disabled] {
      background-color: $element-ui-3;
    }

    &::after {
      background-color: $element-text-inverse;
      opacity: 1;
    }
  }

  &:indeterminate {
    border-color: $element-ui-0;

    &::after {
      mask-image: none;
      background-color: $element-ui-0;
      inline-size: 10px;
      block-size: 2px;
      opacity: 1;
      inset-inline-start: 2px;
      inset-block-start: 6px;
    }

    &:hover {
      border-color: $element-ui-0-hover;

      &::after {
        background-color: $element-ui-0-hover;
      }
    }

    &:disabled {
      border-color: $element-ui-3;

      &::after {
        background-color: $element-ui-3;
      }
    }
  }
}

%form-check-radio {
  inline-size: $form-radio-size;
  block-size: $form-radio-size;
  border-radius: 50%;
  inset-inline-start: 0;
  inset-block-start: map-get($spacers, 2);

  &::after {
    content: '';
    position: absolute;
    display: block;
    inset-block-start: $form-radio-offset;
    inset-inline-start: $form-radio-offset;
    inline-size: $form-radio-inner-size;
    block-size: $form-radio-inner-size;
    border-radius: 50%;
  }

  &:checked {
    &:not([disabled]) {
      border-color: $element-ui-0;

      &::after {
        background-color: $element-ui-0;
      }

      &:hover {
        border-color: $element-ui-0-hover;

        &::after {
          background-color: $element-ui-0-hover;
        }
      }
    }

    &[disabled]::after {
      background-color: $element-ui-3;
    }
  }
}

%form-check-label {
  display: inline-block;
  position: relative;
  padding-block: $form-check-padding-y;
  padding-inline: map-get($spacers, 4) $form-check-padding-x;
  border-radius: $element-radius-2;
  color: $element-text-primary;
}

.form-check-input {
  @extend %form-check-base;

  ~ .form-check-label {
    @extend %form-check-label;
  }

  &[disabled] ~ .form-check-label {
    color: $element-text-disabled;
  }

  &[type='checkbox'] {
    @extend %form-check-checkbox;
  }

  &[type='radio'] {
    @extend %form-check-radio;
  }
}

// button group
.btn-check {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  pointer-events: none;

  &[disabled],
  &:disabled {
    + .btn {
      pointer-events: none;
      filter: none;
      color: $element-text-disabled;
    }
  }
}

.ngx-datatable .datatable-checkbox {
  inline-size: $form-check-size !important; // stylelint-disable-line declaration-no-important
  block-size: $form-check-size !important; // stylelint-disable-line declaration-no-important
  inset-block-start: 1px !important; // stylelint-disable-line declaration-no-important

  input[type=checkbox] {
    @extend %form-check-base;
    @extend %form-check-checkbox;
  }
}

formly-field-multicheckbox {
  input[type=checkbox] {
    @extend %form-check-base;
    @extend %form-check-checkbox;

    ~ label {
      @extend %form-check-label;
    }

    &[disabled] ~ label {
      color: $element-text-disabled;
    }
  }
}

formly-field-radio {
  input[type=radio] {
    @extend %form-check-base;
    @extend %form-check-radio;

    ~ label {
      @extend %form-check-label;
    }

    &[disabled] ~ label {
      color: $element-text-disabled;
    }
  }
}
